/* global window, document */
import { h } from './component/element';
import DataProxy from './core/data_proxy';
import Sheet from './component/sheet';
import Bottombar from './component/bottombar';
import { cssPrefix } from './config';
import { locale } from './locale/locale';
import { addCustomFormula } from './core/formula';
import './index.less';

const clickableElementFinders = [];
function addClickableElementFinder(finder, render, onCtrlClick) {
  clickableElementFinders.push({ finder, render, onCtrlClick });
}

const choosers = [];
function addChooser(getHint, onTriggered) {
  choosers.push({ getHint, onTriggered });
}


class Spreadsheet {
  constructor(selectors, options = {}) {
    let targetEl = selectors;
    this.options = { showBottomBar: true, ...options };
    this.sheetIndex = 1;
    this.datas = [];
    if (typeof selectors === 'string') {
      targetEl = document.querySelector(selectors);
    }
    this.bottombar = this.options.showBottomBar ? new Bottombar(() => {
      if (this.options.mode === 'read') return;
      const d = this.addSheet();
      this.sheet.resetData(d);
    }, (index) => {
      const d = this.datas[index];
      this.sheet.resetData(d);
    }, () => {
      this.deleteSheet();
    }, (index, value) => {
      this.datas[index].name = value;
      this.sheet.trigger('change');
    }) : null;
    this.data = this.addSheet();
    const rootEl = h('div', `${cssPrefix}`)
      .on('contextmenu', evt => evt.preventDefault());
    // create canvas element
    targetEl.appendChild(rootEl.el);
    this.sheet = new Sheet(rootEl, this.data, this);
    if (this.bottombar !== null) {
      rootEl.child(this.bottombar.el);
    }
    if (!window.xSpreadsheetCtrlKeyListener) {
      let isBodyClassSet = false;
      window.xSpreadsheetCtrlKeyListener = evt => {
        let hasCtrlKey = evt.ctrlKey || evt.metaKey;
        if (hasCtrlKey !== isBodyClassSet) {
          isBodyClassSet = hasCtrlKey;
          document.body.classList.toggle(`${cssPrefix}-has-ctrl-key`, hasCtrlKey);
          document.body.classList.toggle(`${cssPrefix}-no-ctrl-key`, !hasCtrlKey);
        }
      };
      window.addEventListener('mousemove', window.xSpreadsheetCtrlKeyListener);
      window.addEventListener('keydown', window.xSpreadsheetCtrlKeyListener);
      window.addEventListener('keyup', window.xSpreadsheetCtrlKeyListener);
    }
  }
  
  focus() {
    if (this.sheet) {
      this.sheet.focus();
    }
  }

  addSheet(name, active = true) {
    const n = name || `sheet${this.sheetIndex}`;
    const d = new DataProxy(n, this.options);
    d.change = (...args) => {
      this.sheet.trigger('change', ...args);
    };
    this.datas.push(d);
    // console.log('d:', n, d, this.datas);
    if (this.bottombar !== null) {
      this.bottombar.addItem(n, active, this.options);
    }
    this.sheetIndex += 1;
    return d;
  }

  deleteSheet() {
    if (this.bottombar === null) return;

    const [oldIndex, nindex] = this.bottombar.deleteItem();
    if (oldIndex >= 0) {
      this.datas.splice(oldIndex, 1);
      if (nindex >= 0) this.sheet.resetData(this.datas[nindex]);
      this.sheet.trigger('change');
    }
  }

  loadData(data) {
    const ds = Array.isArray(data) ? data : [data];
    if (this.bottombar !== null) {
      this.bottombar.clear();
    }
    this.datas = [];
    if (ds.length > 0) {
      for (let i = 0; i < ds.length; i += 1) {
        const it = ds[i];
        const nd = this.addSheet(it.name, i === 0);
        nd.setData(it);
        if (i === 0) {
          this.sheet.resetData(nd);
        }
      }
    }
    return this;
  }

  getData() {
    return this.datas.map(it => it.getData());
  }

  cellText(ri, ci, text, sheetIndex = 0) {
    this.datas[sheetIndex].setCellText(ri, ci, text, 'finished');
    return this;
  }

  cell(ri, ci, sheetIndex = 0) {
    return this.datas[sheetIndex].getCell(ri, ci);
  }

  cellStyle(ri, ci, sheetIndex = 0) {
    return this.datas[sheetIndex].getCellStyle(ri, ci);
  }
  
  setSelectedCellCustomFormatter(formatter) {
    this.sheet.setSelectedCellCustomFormatter(formatter);
  }
  
  clearSelectedCellCustomFormatter() {
    this.sheet.clearSelectedCellCustomFormatter();
  }
  
  getSelectedRange(sheetIndex = 0) {
    const range = this.datas[sheetIndex].selector.range;
    return {
      col0: range.sci,
      col1: range.eci,
      row0: range.sri,
      row1: range.eri,
    };
  }
  
  setSelectedRange(range) {
    this.sheet.selectorSet(false, range.row0, range.col0, true, false);
    this.sheet.selectorSet(true, range.row1, range.col1, true, false);
  }
  
  clearCustomClipboard() {
    this.sheet.clearClipboard();
  }

  reRender() {
    this.sheet.table.render();
    return this;
  }

  on(eventName, func) {
    this.sheet.on(eventName, func);
    return this;
  }

  validate() {
    const { validations } = this.data;
    return validations.errors.size <= 0;
  }

  change(cb) {
    this.sheet.on('change', cb);
    return this;
  }
  
  getClickableElementFinders() {
    return clickableElementFinders;
  }
  
  getChoosers() {
    return choosers;
  }

  static locale(lang, message) {
    locale(lang, message);
  }
  
  static addCustomFormula(key, title, render) {
    addCustomFormula(key, title, render);
  }
  
  static addClickableElementFinder(finder, render, onCtrlClick) {
    addClickableElementFinder(finder, render, onCtrlClick);
  }
  
  static addChooser(getHint, onTriggered) {
    addChooser(getHint, onTriggered);
  }
}

const spreadsheet = (el, options = {}) => new Spreadsheet(el, options);

if (window) {
  window.x_spreadsheet = spreadsheet;
  window.x_spreadsheet.locale = (lang, message) => locale(lang, message);
  window.x_spreadsheet.addCustomFormula = (key, title, render) => addCustomFormula(key, title, render);
  window.x_spreadsheet.addClickableElementFinder = (finder, render, onCtrlClick) => addClickableElementFinder(finder, render, onCtrlClick);
  window.x_spreadsheet.addChooser = (getHint, onTriggered) => addChooser(getHint, onTriggered);
}

export default Spreadsheet;
export {
  spreadsheet,
};
